/* Scroll bar
------------------------------------------------------- */
.cmap-container .cmap-scroll-styled {
  overflow: auto;
}
.cmap-container .cmap-scroll-styled::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  border-left: 0;
  background: rgba(0, 0, 0, 0.1);
}
.cmap-container .cmap-scroll-styled::-webkit-scrollbar:hover {
  background: rgba(0, 0, 0, 0.15);
}
.cmap-container .cmap-scroll-styled::-webkit-scrollbar-track {
  background: none;
}
.cmap-container .cmap-scroll-styled::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0;
}

/* Loading overlay
------------------------------------------------------- */
.cmap-loading:after,
.cmap-loading:before {
  content: '';
  display: block;
  position: absolute;
  z-index: 10;
}
.cmap-loading:before {
  background: transparent;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.cmap-loading:after {
  background: rgba(0, 0, 0, 0.2) url(data:image/svg+xml;base64,) 50% 50% no-repeat;
  left: 50%;
  top: 50%;
  margin: -20px 0 0 -20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-animation: rotate 1s linear infinite;
  animation: rotate 1s linear infinite;
}
.cmap-spinner:after {
  content: '';
  position: absolute;
  right: 5px;
  top: 50%;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5) url(data:image/svg+xml;base64,) 50% 50% no-repeat;
  background-size: 20px;
  margin: -15px 0 0 -15px;
  width: 30px;
  height: 30px;
  opacity: .5;
  -webkit-animation: rotate 1s linear infinite;
  animation: rotate 1s linear infinite;
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.pointer {
  cursor: pointer;
}


